.form-popup-container {
}

@media only screen and (max-height: 1100px) {
  .form-popup-container {
    max-height: 700px;
    overflow: initial;
  }
}

@media only screen and (max-height: 850px) {
  .form-popup-container {
    height: 600px;
  }
}

@media only screen and (max-height: 750px) {
  .form-popup-container {
    height: 590px;
  }
}

@media only screen and (max-height: 700px) {
  .form-popup-container {
    height: 550px;
  }
}

@media only screen and (max-height: 600px) {
  .form-popup-container {
    height: 460px;
  }
}

@media only screen and (max-width: 960px) {
  .form-popup-container {
    width: 600px;
  }
}

@media only screen and (max-width: 730px) {
  .form-popup-container {
    width: 500px;
  }
}

@media only screen and (max-width: 560px) {
  .form-popup-container {
    width: 400px;
    padding: 10px;
    .directly-transfer-section {
      margin-top: 15px;
      svg {
        width: 30px;
        height: 30px;
      }
      .directly-transfer-text {
        font-size: 19px;
      }
    }
    .select-bank-text {
      font-size: 20px;
    }
    .header-title {
      text-align: center;
      font-size: 23px;
    }
    .bank-name {
      margin-top: 10px;
    }
    .account-title-text {
      font-size: 18px;
    }
    .account-title-section {
      padding-left: 10px;
      .account-detail-text {
        word-break: break-all;
        font-size: 17px;
      }
    }
    .button-copy {
      width: 100%;
    }
    .register-text {
      font-size: 18px;
    }
    .register-example {
      font-size: 18px;
    }
    .note-pay {
      text-align: center;
      font-size: 19px;
    }
  }
}

