.gb-information-card {
  width: 454px;
  height: 241px;
  background: url('../../../assets/images/card-info-background.svg');
  border-radius: 12px;
  padding: 38px;
  justify-content: space-between;
  flex-direction: column;
  display: flex;
  .box-card {
    background: linear-gradient(108.1deg, #00adbb 7.45%, #0395a0 91.1%);
    box-shadow: 0px 5px 16px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
    width: 100%;
    height: 100%;
    z-index: 2;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.1;
  }
  .box-coin-membership {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  .box-coin {
    display: flex;
    align-items: center;
    color: #e5c35c;
  }
  .icon-coin {
    width: 23px;
    height: 23px;
    margin: 0px 4px;
  }
  .box-logo {
    width: 107px;
    padding-top: 4px;
  }
  .box-membership {
    width: 147px;
  }
  .icon-membership {
    width: 100%;
  }
  .icon-logo {
    width: 100%;
  }
  .text-money {
    font-family: 'Google Sans', sans-serif;
    font-size: 18px;
    line-height: 30px;
    color: #e5c35c;
  }
  .box-text-number-card {
    text-align: justify;
    overflow: hidden;
    padding-top: 10px;
  }
  .text-number-card {
    font-family: 'Google Sans', sans-serif;
    font-size: 33px;
    display: contents;
    height: 40px;
    color: #e5c35c;
  }
  .text-number-card::after {
    content: '';
    display: inline-block;
    width: 100%;
    height: auto;
    padding-top: 40px;
  }
  .name-card-text {
    font-family: 'Google Sans', sans-serif;
    font-size: 20px;
    line-height: 30px;
    color: #e5c35c;
  }
  .expired_date {
    font-family: 'Google Sans', sans-serif;
    line-height: 30px;
    font-size: 20px;
    color: #e5c35c;
  }
  .box-register-date-and-user-name {
    justify-content: space-between;
    align-items: center;
  }
  .text-date {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 12px;
    line-height: 18px;
    color: white;
    margin-right: 6px;
    margin-bottom: 0px;
    color: #fef09e;
  }

  @media only screen and (max-width: 320px) {
    .text-money {
      font-size: 15px;
    }
    .text-date {
      font-size: 11px;
    }
    .expired_date {
      font-size: 15px;
    }
    .name-card-text {
      font-size: 15px;
    }
    .text-number-card {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 510px) {
  .gb-information-card {
    padding: 20px;
    width: 390px;
    height: 240px;
    .text-number-card {
      font-size: 24px;
    }
    .text-date {
      font-size: 13px;
    }
    .expired_date {
      font-size: 18px;
    }
    .name-card-text {
      font-size: 17px;
    }
  }
}

@media screen and (max-width: 455px) {
  .gb-information-card {
    width: max-content;
    height: 240px;
  }
}
@media only screen and (max-width: 420px) {
  .text-money {
    font-size: 15px;
  }
  .text-date {
    font-size: 9px;
  }
  .expired_date {
    font-size: 12px;
  }
  .name-card-text {
    font-size: 12px;
  }
  .text-number-card {
    font-size: 18px;
  }
}

@media screen and (max-width: 430px) {
  .gb-information-card {
    width: auto;
    height: 200px;
    .text-number-card {
      font-size: 20px;
    }
    .text-date {
      font-size: 11px;
    }
    .expired_date {
      font-size: 16px;
    }
    .name-card-text {
      font-size: 15px;
      padding-left: 10px;
    }
  }
}

@media screen and (max-width: 380px) {
  .gb-information-card {
    .text-number-card::after {
      padding-top: 20px;
    }
    .box-text-number-card {
      padding-top: unset;
    }
    .name-card-text {
      font-size: 13px;
    }
    .text-date {
      font-size: 10px;
    }
    .expired_date {
      font-size: 15px;
    }
  }
}

@media screen and (max-width: 360px) {
  .gb-information-card {
    .box-coin-membership {
      margin-top: 10px;
    }
    .text-number-card {
      font-size: 18px;
    }
    .text-number-card::after {
      padding-top: 20px;
    }
    .box-text-number-card {
      padding-top: 10px;
    }
    .name-card-text {
      font-size: 12px;
    }
    .text-date {
      font-size: 9px;
    }
    .expired_date {
      font-size: 13px;
    }
  }
}

@media screen and (max-width: 340px) {
  .gb-information-card {
    .box-coin-membership {
      margin-top: 10px;
    }
    .text-number-card {
      font-size: 16px;
    }
    .text-number-card::after {
      padding-top: 20px;
    }
    .box-text-number-card {
      padding-top: 10px;
    }
    .name-card-text {
      font-size: 12px;
    }
    .text-date {
      font-size: 9px;
    }
    .expired_date {
      font-size: 13px;
    }
  }
}

@media screen and (max-width: 320px) {
  .gb-information-card {
    height: 170px;
    .box-coin-membership {
      margin-top: 10px;
    }
    .text-number-card {
      font-size: 13px;
    }
    .text-number-card::after {
      padding-top: 20px;
    }
    .box-text-number-card {
      padding-top: unset;
    }
    .name-card-text {
      font-size: 9px;
    }
    .text-date {
      font-size: 9px;
    }
    .expired_date {
      font-size: 10px;
    }
  }
}

@media screen and (max-width: 280px) {
  .gb-information-card {
    height: 180px;
    width: 200px;
    .box-coin-membership {
      margin-top: 10px;
    }
    .text-number-card {
      font-size: 13px;
    }
    .text-number-card::after {
      padding-top: 20px;
    }
    .box-text-number-card {
      padding-top: 10px;
    }
    .name-card-text {
      font-size: 9px;
    }
    .text-date {
      font-size: 9px;
    }
    .expired_date {
      font-size: 10px;
    }
  }
}
