.bank-container-item {
  width: 100%;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  flex-direction: row;
  position: relative;
  background-color: var(--white);
  .icon {
    position: absolute;
    top: 5px;
    right: 5px;
  }
  .button {
    background-color: var(--white);
    width: 100%;
    height: 72px;
    border: 1px solid #dfe2e7;
    border-radius: 7px;
  }
  .selected_button {
    background-color: 'var(--white)';
    width: 100%;
    height: 72px;
    border: 2px solid #c4a732;
    border-radius: 7px;
    outline: 'none';
  }
  .bank_image {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
