.amount-icon {
  circle {
    fill: #c4a732;
  }
}
.point-payment-popup-root {
  overflow: scroll;
}
.point-payment-popup-container {
  .error {
    margin-top: 16px;
    color: #fe5466;
  }
  .title-payment {
    font-size: 24px;
    padding-bottom: 10px;
  }
  .content-container {
    padding-inline: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .money-need-to-pay-container {
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    .number-money-need-to-pay {
    }
  }
  .number-equal-e-coin-pay-container {
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    margin-top: 20px;
  }
  .input-money-container {
    width: 100%;
    margin-top: 10px;
    .input-money {
      color: #ff4355;
    }
    .deposit-amount-icon {
      circle {
        fill: #ff4355;
      }
    }
  }
  .input-e-coin-container {
    width: 100%;
    margin-top: 10px;
  }
  .button-cash {
    margin-top: 10px;
    margin-bottom: 20px;
  }
}

@media only screen and (max-height: 800px) {
  .point-payment-popup-root {
    overflow: scroll;
  }
