.payment-container {
  width: min-content;
  .payment-title {
    font-size: 28px;
    font-family: 'Google Sans';
    font-weight: 700;
  }
  .content-section {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 25px;
  }
  .infor-item-payment-container {
    width: 368px;
    height: 135px;
    border-radius: 15px;
    border: 1px solid #f5f6f8;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 20px;
    justify-content: space-between;
  }
}

@media only screen and (max-height: 600px) {
  .payment-container {
    height: 450px;
  }
}

@media only screen and (max-width: 850px) {
  .payment-container {
    width: 600px;
